/**
 *
 * @param {row of each symbol} props
 * @returns
 */
const AlertRow = (props) => {
  const { symbol, alert, symbolData, binanceHeatmap } = props;
  const { candles, fundingRates, openInterests, rsi, tickers } = symbolData;
  const binanceUrl = `https://www.binance.com/en/futures/${symbol}`;
  const change24h = _.find(
    binanceHeatmap.tradingVol24H,
    (contract, idx) => contract.symbol === symbol
  );
  // const volume24h = contract24hData?.quoteVolume || "NA";
  //
  const openInNewTab = (url) => {
    const newWindow = window.open(url, "_blank", "noopener,noreferrer");
    if (newWindow) newWindow.opener = null;
  };
  //
  return (
    <CTableRow color="">
      <CTableDataCell
        className="custom-link"
        onClick={() => openInNewTab(binanceUrl)}
      >
        <h7>{DateFormatter(alert.time)}</h7>
        <div className="w-100"></div>
        <h7>{alert.alertType}</h7>
        <div className="w-100"></div>
        <Symbol symbol={symbol} />
      </CTableDataCell>
      <CTableDataCell>
        <AlertPrice tickers={tickers} />
        {/* </CTableDataCell>
      <CTableDataCell> */}
        <AlertVolume tickers={tickers} change24h={change24h} />
      </CTableDataCell>
      <CTableDataCell>
        <AlertOI openInterests={openInterests["tickers"]} />
        {/* </CTableDataCell>
      <CTableDataCell> */}
        <AlertRsi rsi={rsi} />
      </CTableDataCell>
      <CTableDataCell>
        <AlertFundingRate fundingRates={fundingRates} />
      </CTableDataCell>
    </CTableRow>
  );
};

const BotAlert = (props) => {
  const alert = props.alert;
  const binanceUrl = `https://www.binance.com/en/futures/${alert.symbol}`;
  return (
    <CCard
      className="mx-3 mb-3"
      color="warning"
      textColor="white"
      style={{ maxWidth: "24rem" }}
    >
      <a
        href={binanceUrl}
        target="_blank"
        rel="noopener noreferrer"
        className="custom-link"
      >
        <CCardHeader>
          <div className="d-flex d-flex-row justify-content-between">
            <h5>
              <strong>{alert.symbol}</strong>
            </h5>
            <div>{dayjs(alert.time).format("DD/MM HH:mm:ss")}</div>
          </div>
        </CCardHeader>
        <CCardBody>
          {alert.alertType === "PRICE_CHANGE" && (
            <>
              <div className="d-flex d-flex-row justify-content-between">
                <div>{alert.alertType}</div>
                <div>{PercentChangeFormatter(alert.priceChange)}</div>
              </div>
              <div className="d-flex d-flex-row justify-content-between">
                <div>Timeframe</div>
                <div>{alert.timeframe}</div>
              </div>
              <div className="d-flex d-flex-row justify-content-between">
                <div>Price</div>
                <div>{CurrencyFormatter.format(alert.price)}</div>
              </div>
            </>
          )}
          {alert.alertType === "RSI" && (
            <>
              <div className="d-flex d-flex-row justify-content-between">
                <div>{alert.rsiType}</div>
                <div>{alert.rsi}</div>
              </div>
              <div className="d-flex d-flex-row justify-content-between">
                <div>Timeframe</div>
                <div>{alert.timeframe}</div>
              </div>
            </>
          )}
          {alert.alertType === "OI_CHANGE" && (
            <>
              <div className="d-flex d-flex-row justify-content-between">
                <div>{alert.alertType}</div>
                <div>{PercentChangeFormatter(alert.realTimeChange)}</div>
              </div>
              <div className="d-flex d-flex-row justify-content-between">
                <div>Timeframe</div>
                <div>{alert.timeframe}</div>
              </div>
              <div className="d-flex d-flex-row justify-content-between">
                <div>Open Interest</div>
                <div>
                  {CurrencyFormatter.format(alert.sumOpenInterestValue)}
                </div>
              </div>
            </>
          )}
        </CCardBody>
      </a>
    </CCard>
  );
};

const StackingAlertToast = (props) => {
  const { alertToasts, onCloseHandler } = props;
  //
  const closeAlert = (index) => {
    console.log("closeAlert", index);
    onCloseHandler(index);
    // $alertToats((prevToats) => _.pull(prevToats, prevToats[index]));
  };
  //
  return (
    <ToastContainer className="position-static" position="bottom-end">
      {!_.isEmpty(alertToasts) &&
        alertToasts.map((index) => {
          return (
            <Toast
              className="my-2 mx-2"
              bg="warning"
              key={index}
              onClose={() => closeAlert(index)}
            >
              <Toast.Header>
                <img
                  src="holder.js/20x20?text=%20"
                  className="rounded me-2"
                  alt=""
                />
                <strong className="me-auto">
                  {`${index} symbol timeframe`}{" "}
                </strong>
                <small className="text-muted">{DateNow()}</small>
              </Toast.Header>
              <Toast.Body className="text-black">
                <div>Price/OI/RSI:</div>
                <div>Change:%</div>
                <div>Just like this</div>
              </Toast.Body>
            </Toast>
          );
        })}
    </ToastContainer>
  );
};

const BinanceAlerts = () => {
  const { sendMessage, lastMessage, readyState } = useWebSocket(socketUrl, {
    share: true,
    retryOnError: true,
    reconnectAttempts: 1000,
    reconnectInterval: 3000,
    //Will attempt to reconnect on all close events, such as server shutting down
    shouldReconnect: () => true,
    onOpen: (event) => console.log("opened ws", event),
    onClose: (closeEvent) => console.log("ws closed", closeEvent),
    onError: (errEvent) => console.log("error", errEvent),
    onReconnectStop: (numAttemp) => console.log("reconnect stopped", numAttemp),
  });
  //
  const [alertToasts, $alertToasts] = useState([]);
  const [alertSymbList, $alertSymbList] = useState([]);
  const [binanceAlerts, $binanceAlerts] = useState({});
  const [alertDataList, $alertDataList] = useState([]);
  const [binanceHeatmap, $binanceHeatmap] = useState({});
  //
  useEffect(() => {
    if (lastMessage !== null) {
      const json = JSON.parse(lastMessage.data);
      /**
       * const alertData = {
    symbols: alertSymbList,
    alertlist: binanceAlerts,
    marketdata: alertDataList,
  };
       */
      let respAlertList = json.alertData.alertlist;
      _.reverse(respAlertList);
      $binanceAlerts((prevAlerts) => respAlertList);
      // $alertToasts((prevToasts) => _.takeRight(respAlertList, 5));
      let respSymbList = json.alertData.symbols;
      _.reverse(respSymbList);
      $alertSymbList((prevList) => respSymbList);
      const marketData = json.alertData.marketdata;
      $alertDataList((prevList) => marketData);
      $binanceHeatmap(json.binanceHeatmap);
    }
  }, [lastMessage]);
  //
  function alertToastClose(index) {
    $alertToasts((prevAlerts) => {
      const newAlerts = prevAlerts.splice(index, 1);
      console.log(newAlerts, newAlerts);
      return newAlerts;
    });
  }

  return (
    <>
      {_.isEmpty(alertSymbList) && <div>No Alert Detected</div>}
      {/* {!_.isEmpty(alertSymbList) && (
        <>
          {alertSymbList.map((symbol, index) => {
            const alert = _.find(
              binanceAlerts,
              (alert) => alert.symbol === symbol
            );
            const symbolData = _.find(
              alertDataList,
              (obj) => obj.symbol === symbol
            );
            return (
              <AlertCard
                key={index}
                symbol={symbol}
                timestamp={alert.time}
                symbolData={symbolData.data}
              />
            );
          })}
        </>
      )} */}
      {!_.isEmpty(alertSymbList) && (
        <CTable striped hover responsive color="light" className="smaller-text">
          <CTableHead color="dark">
            <CTableRow>
              <CTableHeaderCell>Symbol</CTableHeaderCell>
              <CTableHeaderCell>Price / Volume</CTableHeaderCell>
              {/* <CTableHeaderCell>Volume</CTableHeaderCell> */}
              <CTableHeaderCell>Open Interest / RSI</CTableHeaderCell>
              {/* <CTableHeaderCell>RSI</CTableHeaderCell> */}
              <CTableHeaderCell>Funding Rate</CTableHeaderCell>
            </CTableRow>
          </CTableHead>
          <CTableBody>
            {alertSymbList.map((symbol, index) => {
              const alert = _.find(
                binanceAlerts,
                (alert) => alert.symbol === symbol
              );
              const symbolData = _.find(
                alertDataList,
                (obj) => obj.symbol === symbol
              );
              return (
                <AlertRow
                  key={index}
                  symbol={symbol}
                  alert={alert}
                  symbolData={symbolData.data}
                  binanceHeatmap={binanceHeatmap}
                />
              );
            })}
          </CTableBody>
        </CTable>
      )}
      <div className="d-flex flex-row flex-wrap justify-content-around align-content-around">
        {!_.isEmpty(binanceAlerts) &&
          binanceAlerts.map((alert) => {
            return (
              <CCol sm={4}>
                <BotAlert alert={alert} />
              </CCol>
            );
          })}
      </div>
      {/* <StackingAlertToast
        alertList={alertToasts}
        onCloseHandler={alertToastClose}
      /> */}
    </>
  );
};

export default BinanceAlerts;
